    if(hiddenNodesCount > 5){
      hiddenNodesCount = '5+';
    }
    //console.log(outHiddenNodes);
    const rand = Math.random();

    d.model.addNodeData({
      key: currentNode.key + '_mega' + rand,
      data: currentNode.data,
      outHiddenNodes: outHiddenNodes,
      intoHiddenNodes: intoHiddenNodes,
      outNodesCount: outNodesCount,
      intoNodesCount: intoNodesCount,
      hiddenNodesCount: hiddenNodesCount,
      category: 'megaNode',
      oldCategory: 'tech',
      opacity: 1,
      group: group
    });

