  setGraphTheme = (): void => { 
    this.diagram.commit((d) => {
      d.nodes.each((node) => {
        if(!node.data.isShift){
          d.model.set(node.data, "strokeColor", getNodeStrokeColor(node.data, this.appStore.theme));
          d.model.set(node.data, "textColor", getTextColor(this.appStore.theme));
          d.model.set(node.data, "backgroundColor", getBackgroundColor(this.appStore.theme));
          d.model.set(node.data, "nodeColor", getNodedColor(this.appStore.theme));
        }else{
          node.opacity = 0;
        }
      });

      d.links.each((link) => {
        if(!link.data.isShift){
          d.model.set(link.data, "startNodeColor", link.fromNode.data.strokeColor);
          d.model.set(link.data, "endNodeColor", link.toNode.data.strokeColor);
          d.model.set(link.data, "strokeColor", getEdgeStrokeColor(link.data, this.appStore.theme));
          d.model.set(link.data, "backgroundColor", getBackgroundColor(this.appStore.theme));
        }else{
          link.opacity = 0;
        }
      });
    });
    this.setGrid(this.diagram);
  }

  potentialMode: boolean = false;
  potentialNodes: any = null;
  currentPotentialNode: any = null;
  currentPotentialRisk: any = null;

  changePotentialMode = () => {
    if(this.potentialMode){
      this.potentialMode = false;

    }else{
      this.potentialMode = true;
      this.zvMode = false;
    }
    this.drawNodes();
  }

  changePotentialVisible = (node: any, risk: any): void => {
    if (this.currentPotentialRisk && this.currentPotentialRisk.RiskId === risk.RiskId) {
      this.currentPotentialNode = null;
      this.currentPotentialRisk = null;
      this.potentialNodes = null;
      
    } else {
      this.currentPotentialNode = node; 
      this.currentPotentialRisk = risk;
      this.potentialNodes = risk.ConnectedNodes;
      
    }
    this.drawNodes();
  }

  setPotentialVisible = (node: any, risk: any): void => {
    this.currentPotentialNode = node; 
    this.currentPotentialRisk = risk;
    this.potentialNodes = risk.ConnectedNodes;

    this.drawNodes();
  }

  private setPotential = (): void => {  
    this.rightMenuStore.setClose();
    this.infoCardStore.setClose();

    this.diagram.layout = new go.Layout();
    this.diagram.clearSelection();

    this.diagram.model.commit((m) => {
        m.nodeDataArray.forEach((data) => {
          const nodeIsConnected: go.Node = this.potentialNodes.find((node) => node === data.orig_id);

          if (nodeIsConnected && data.category !== 'clone' && !data.hidden) {
            this.diagram.findNodeForKey(data.key).opacity = 1;
            (m as go.GraphLinksModel).addLinkData({
              from: this.currentPotentialNode.key,
              to: data.key,
              strokeColor: '#8805ff',
              category: 'potential'
            });
          } else if (this.currentPotentialNode.key === data.key && !data.hidden) {
            this.diagram.findNodeForKey(data.key).opacity = 1;
          } else if(data.hidden){
            this.diagram.findNodeForKey(data.key).opacity = 0;
          }else{
            this.diagram.findNodeForKey(data.key).opacity = 0.1;
          }
        });

        (m as go.GraphLinksModel).linkDataArray.forEach((data) => {
          if (data.category !== 'potential' && !data.hidden) {
            this.diagram.findLinkForData(data).opacity = 0.1;
          }else if (data.hidden){
            this.diagram.findLinkForData(data).opacity = 0;
          }
        });
    });
  }

  private destroyPotential = (): void => {
    this.diagram.links.each((link) => {
      if (link.category === 'potential') {
        this.diagram.remove(link);
      } else if (link.data.hidden) {
        link.opacity = 0;
      }else{
        link.opacity = 1;
      }
    });
    this.diagram.model.commit((m) => {
      m.nodeDataArray.forEach((data) => {
        if (!data.hidden) {
          this.diagram.findNodeForKey(data.key).opacity = 1;
        }else{
          this.diagram.findNodeForKey(data.key).opacity = 0;
        }
      });
    });
  }

  zvMode: boolean = false;
  currentZv: any = null;

  changeZvMode = () => {
    if(this.zvMode){
      this.zvMode = false;
    }else{
      this.zvMode = true;
      this.potentialMode = false;
    }
    this.drawNodes();
  }

  changeZvVisible = (zv: any): void => {
    if (this.currentZv && this.currentZv.name === zv.name) {
      this.currentZv = null;
    } else {
      this.currentZv = zv;
    }
    this.drawNodes();
  }

